.hero {
  margin: 0 auto;
  color: $white;
  position: relative;
  padding: 0;

  @include dw() {
    margin-top: 111px;
  }
}

.hero__header {
  width: 100%;

  @include tw() {
    display: none;
  }

  @include dw() {
    position: absolute;
    display: block;
    width: 720px;
    height: 694px;
    right: 29px;
    top: -281px;
  }
}

.hero__main {
  position: relative;
  top: 40px;
  margin-bottom: 100px;

  @include tw() {
    margin-bottom: 40px;
  }

  div {
    position: absolute;
    top: -120px;
    margin-bottom: 20px;

    @include tw() {
      position: static;
    }
  }
}

.hero__title-slogan {
  @include dw() {
    margin-right: 100px;
  }
}

.hero__title {
  display: flex;
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin-bottom: 25px;
  justify-content: center;

  @include tw() {
    color: $black;
    font-size: 60px;
    line-height: 60px;
    display: block;
    margin-left: 100px;
  }

  @include dw() {
    margin-left: 140px;
    margin-top: 80px;
  }
}

.hero__slogan {
  font-family: $fontoswald;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  display: flex;
  justify-content: center;

  @include tw() {
    color: $black;
    font-size: 20px;
    line-height: 20px;
    margin-left: 100px;
    display: inline-block;
  }

  @include dw() {
    margin-left: 140px;
    margin-bottom: 52px;
  }
}

.hero__img {
  width: 100%;
  margin-top: -190px;

  @include tw() {
    width: 709px;
    height: 609px;
    margin: 0;
    margin-left: 32px;
  }

  @include dw() {
    height: 499px;
    width: 552px;
    position: absolute;
    top: -119px;
    left: 498px;
    z-index: 2;
  }
}

.hero__button {
  display: flex;
  flex-wrap: wrap;
  width: 280px;
  padding-top: 11px;
  padding-bottom: 11px;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: -60px;
  margin-left: 20px;


  @include tw() {
    position: absolute;
    top: 321px;
    left: 102px;
    width: 244px;
    padding-top: 14px;
    padding-bottom: 14px;
    align-items: center;
  }

  @include dw() {
    position: static;
    margin: 0;
    margin-bottom: 267px;
    margin-left: 142px;
  }
}

.hero__button:hover {
  background-color: rgba(94, 170, 47, 1);

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
